* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #f4f4f4;
  padding: 20px;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.search-form {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input {
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.search-form button:hover {
  background-color: #218838;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 10px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.photo-card .info {
  text-align: center;
  padding: 15px;
}

.photo-card .info p {
  margin: 5px 0;
  font-size: 14px;
}

.load-more {
  width: 200px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 40px auto;
  padding: 10px;
  font-size: 16px;
  transition: background-color .3s;
  display: block;
}

.load-more:hover {
  background-color: #0056b3;
}

.load-more:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

.hidden {
  display: none;
}

